import React, { useEffect, useState } from "react";
import "./AdminPanel.css";
import SearchBox from "components/SearchBox";
import DropdownList from "components/SearchBox/DropdownList";
import axios from "utils/api";
import downArrow from "assets/icons/downArrow.svg";
import { Handler } from "leaflet";
export default function AdminPanel() {
  const [users, SetUsers] = useState([]);
  const [spinnerState, setSpinnerState] = useState(-1);
  const [filter, setfilter] = useState({});
  const roles = ["admin", "user"];
  const spinners = [
    {
      placeholder: " ",
      items: users.map((user) => user.username),
    },
  ];
  useEffect(() => {
    const set_users = async () => {
      try {
        const { data } = await axios.get("/admin/users");
        SetUsers(data.results);
        console.log("data.results", data.results);
      } catch (error) {
        console.log(error);
      }
    };

    set_users();
  }, []);

  const handle_update_click = async (e) => {
    e.preventDefault();
    await axios.post("/admin/users", users);
  };

  // const handle_search = async (filter) => {
  //   Object.keys(filter).forEach((key) => {
  //     if (filter[key] === "") delete filter[key];
  //   });
  //   console.log(JSON.stringify(filter));
  //   const params = {
  //     filters: JSON.stringify(filter),
  //     offset: 0,
  //     limit: 5,
  //   };
  //   try {
  //     const { data } = await axios.get("/facilities/filter", { params });
  //     setLocations(data.results);
  //     setStartPosition(data.results[0].geocode);
  //   } catch (error) {
  //     console.log(error);
  //   }
  // };

  return (
    <div className="admin-main-grid">
      <div className="admin-below-grid">
        <div className="tableFixHead">
          <table>
            <thead>
              <tr>
                <th> </th>
                <th> </th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              {users &&
                users.map &&
                users.map((user, key) => {
                  return (
                    <tr key={key}>
                      <td className="eng td-center">{user.fullName}</td>
                      <td className="eng td-center">{user.email}</td>
                      <td className="eng td-center">
                        <div key={key} className="admin-my-input">
                          {user.role}
                          <img
                            className="downArrow"
                            src={downArrow}
                            onClick={() => {
                              setSpinnerState(key === spinnerState ? -1 : key);
                            }}
                            data-selected={key === spinnerState}
                          ></img>
                        </div>
                        <div className="drop-down-lst">
                          {key === spinnerState && (
                            <ul className="spinner-lst">
                              {roles.map((item, key) => (
                                <li
                                  key={key}
                                  onClick={(e) => {
                                    e.preventDefault();
                                    const copy = [...users];
                                    const i = copy.indexOf(user);
                                    copy[i].role = roles[key];
                                    console.log(e);
                                    SetUsers(copy);
                                    setSpinnerState(-1);
                                  }}
                                >
                                  {item}
                                </li>
                              ))}
                            </ul>
                          )}
                        </div>
                      </td>
                    </tr>
                  );
                })}
            </tbody>
          </table>
        </div>
      </div>
      <div className="cheat"></div>
      <div className="after">
        <input
          className="update-btn"
          type="submit"
          value={""}
          onClick={handle_update_click}
        ></input>
      </div>
    </div>
  );
}
